<template>
  <h1 class="h1">
    <!--//返回按钮的分发器-->
    <slot name="back"></slot>
    {{title}}
  </h1>
</template>
<script>
  export default{
    props: ['title']
  }
</script>
<style>
  .h1 {
    width: 100%;
    height: 42px;
    background: #4b4b4b;
    color: #ffffff;
    text-align: center;
    line-height: 42px;
    font-size: 18px;
    position: fixed;
    top: 0px;
    z-index: 100;
  }
</style>



