<template>
	<div class="special">
		<ul class="special-list">
			<li v-for="(item,index) in Data" >
				<img :src="item.pic">
				<p>{{item.title}}</p>
				<h3>¥{{item.salesPrice}}</h3>
				<span>原价:{{item.marketPrice}}</span>
			</li>
		</ul>
	</div>
</template>
<style type="text/css">
	.special{
		width: 100%;
		margin-top:84px;
	}
	.special-list{
		display: flex;
		flex-flow: row wrap;
		width: 100%;
		background: red;
		background: #f2f2f2;
		justify-content:space-around;
	}
	.special-list>li{
		display: inline-block;
		flex-basis: 48%;
		margin-top: 5px;
		height: 250px;
		background: white;
		text-align: left;
		box-shadow: 0 0 2px 2px #E4E7E5;
	}
	.special-list>li img{
		display: block;
		width: 100%;
		height: 150px;
	}
	.special-list>li p{
		font-size: 16px;
		line-height: 25px;
		padding: 10px;
	}
	.special-list>li h3{
		display: inline-block;
		font-size: 16px;
		padding: 0px 10px 0px 10px;
		color: red;
		font-weight: bolder;
	}
	.special-list>li span{
		color: #666666;
		text-decoration: line-through;
		font-size: 14px;
	}
</style>
<script type="text/javascript">
import SpecialData from "../data/selldata.json";
var Data = SpecialData.list.pageList;
var arr = [];//排序用
var arr1 = [];
for (var i = 0; i < Data.length; i++) {//处理字体过长
	var title = Data.title
	if(Data[i].title.length>8){
		Data[i].title = Data[i].title.slice(0,8)+"..." + Data[i].title.slice(Data[i].title.length-5,Data[i].title.length);
	};
	arr[i] = Data[i];
	arr1[i] = Data[i];
}
var sort = function(value,ele){//升序排序函数
	for (var i = 0; i < ele.length; i++) {
		for (var j = i+1; j < ele.length; j++) {
			var nub1 = Number(ele[i][value]);
			var nub2 = Number(ele[j][value]);
			if (nub1>nub2) {
				var agent = ele[j];
				ele[j] = ele[i];
				ele[i] = agent;
			}
		}
	}
};
sort("salesPrice",arr);//价格从小到大
sort("volume",arr1);//销量从低到高
export default{
	props:{
        h1:Boolean,
        h2:Boolean
    },
    data(){
    	return{
    		Data:Data,
    		arr:arr,
    		arr1:arr1
    	}
    },
    watch: {
        h1:function(){//监听价格
        	if (this.h1) {
        		this.arr.reverse();//倒序排列
        		this.Data = this.arr;
        	}else{
        		this.arr.reverse();//倒序排列
        		this.Data = this.arr;
        	}
		},
		h2:function(){//监听销量
        	if (this.h2) {
        		this.arr1.reverse();//倒序排列
        		this.Data = this.arr1;
        	}else{
        		this.arr1.reverse();//倒序排列
        		this.Data = this.arr1;
        	}
		},
    }
}




</script>



