<template>
	<transition name="fade">
		<div v-if="bol" class="intro" v-show="!introShowBol">
			<v-header title="详情页">
				<span slot="back" class="back" @click="back()"> < </span>
			</v-header>
			<div class="intro-h">
				<img :src="introData.pic" class="intro-h-icon1">
				<a href="javascript:void(0);" class="intro-h-icon2"></a>
				<a href="javascript:void(0);" class="intro-h-icon3"></a>
			</div>
			<div class="intro-c">
				<ul class="c-main">
					<li>特卖：新功电热水壶1L进口304不锈钢S5自动断电防干烧</li>
					<li>
						<span style="color: red">¥{{introData.salesPrice}}</span>
						<span>原价:¥99.0</span>
						<span style="text-align: right;">销量:{{introData.volume}}</span>
					</li>
					<li>
						<span>限时:已过期</span>
						<span style="text-align: right;">限量:{{introData.marketPrice}}</span>
					</li>
				</ul>
				<ul class="c-section">
					<li class="details" @click="show1()" :class="{line:show1Bol}">商品详情</li>
					<li class="evaluate" @click="show2()" :class="{line:show2Bol}">用户评价</li>
				</ul>
				<!-- 商品详情页 -->
				<ul class="details-ul" v-show="show1Bol">
					<li v-html="introData.content"></li>
				</ul>
				<!-- 用户评价页 -->
				<ul class="evaluate-ul" v-show="show2Bol">
					<li>
						<ul class="eval-c">
							<li v-for="item in allData">
								<span><span class="name">姓名:<span style="color: black;">{{item.name}}</span><br/>
								<span style="color:#666666;margin-left: 10px">{{item.con}}</span>
							</li>
						</ul>
					</li>
				</ul>
			</div>
			<div class="footer">
				<a href="javascript:void(0)">
					<span>1</span>
				</a>
				<span>加入购物车</span>
				<span>立即购买</span>
			</div>
		</div>
	</transition>
</template>
<style type="text/css">
	/*返回键*/
	.back{
		font-size: 30px;
		color: white;
		position: absolute;
		line-height: 42px;
		left: 10px;
	}
	.intro{
		position: absolute;
		width: 100%;
		top: 42px;
		z-index: 100;
		background: #e0e0e0;
	}
	.intro-h,.intro-c{
		width: 100%;
		position: relative;

	}
	.intro .intro-h-icon1{
		display: inline-block;
		width: 100%;
	}
	.intro-h>a{
		display: inline-block;
		width: 30px;
		height: 30px;
		position: absolute;
		top: 10px;
	}
	.intro-h-icon2{
		background: url("../img/allIcon.png") no-repeat;
		background-position: 17% 99%;
		background-size: 300px;
		right: 70px;
	}
	.intro-h-icon3{
		background: url("../img/allIcon.png") no-repeat;
		background-position: 35% 99%;
		background-size: 250px;
		right: 30px;
	}
	.c-main{
		width: 100%;
		padding: 10px;
		position: relative;
		box-sizing: border-box;
		background: white;
	}
	.c-main>li:nth-child(1){
		font-size: 16px;
		color: black;
		line-height: 20px;
	}
	.c-main>li:nth-child(2){
		font-size: 14px;
		color: #666666;
		line-height: 20px;
		border-bottom:1px solid #e0e0e0;
		display: flex;
		margin-top: 20px;
		padding-bottom: 5px;
	}
	.c-main>li:nth-child(3){
		font-size: 14px;
		color: #666666;
		line-height: 20px;
		display: flex;
		padding-top: 5px;
	}
	.c-main>li:nth-child(2)>span{
		flex: 1;
	}
	.c-main>li:nth-child(3)>span{
		flex: 1;
	}
	.c-section{
		width: 100%;
		margin-top: 10px;
		display: flex;
		border-bottom:1px solid #e0e0e0;
	}
	.c-section>li{
		flex: 1;
		height: 42px;
		text-align: center;
		background: white;

		line-height: 42px;
		font-size: 16px;
		position: relative;
		color: #666666;
	}
	.line{
		border-bottom: 2px solid #ff3f12;
		margin-top: -2px;
		box-shadow: 0px 0px 2px 2px #D2D3D5;
	}
	.details-ul,.evaluate-ul{
		width: 100%;
		padding-bottom: 42px;
	}
	.details-ul>li,.evaluate-ul>li,.eval-c{
		width: 100%;
		padding: 5px;
		box-sizing: border-box;
	}
	.details-ul>li>img{
		display: inline-block;
		width: 100%;
	}
	.eval-c>li{
		width: 100%;
		margin-top: 10px;
		background: white;
		font-size: 16px;
		color: #666666;
		line-height: 25px;
		border-radius: 2px;
	}
	.name{
		font-weight: bold;
		color: #D54B3A;
		padding-left: 10px;
	}

	.footer{
		position: fixed;
		bottom: 0px;
		width: 100%;
		height: 42px;
		background: white;
	}
	.footer>a{
		display: inline-block;
		width: 40px;
		height: 33px;
		background: url(../img/allIcon.png) no-repeat;
		background-size: 250px 250px;
		background-position: -1% 101%;
		margin: 6px 0px 0px 10px;
		position: relative;
	}
	.footer>a>span{
		position: absolute;
		top: 0px;
		right: 0px;
		background: #ee3749;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		text-align: center;
		line-height: 20px;
		font-size: 14px;
		color: white;
	}
	.footer>span{
		float: right;
		width: 100px;
		height: 30px;
		margin: 6px 10px;
		font-size: 16px;
		color: white;
		border-radius: 3px;
		text-align: center;
		line-height: 30px;
	}
	.footer>span:nth-child(2){
		background: #f29004;
	}
	.footer>span:nth-child(3){
		background: #ee3749;
	}
</style>
<script type="text/javascript">
import vHeader from "../../../component/header/header.vue";
export default{
	components: {
        vHeader,
    },
    props:{
        introData:{
        	type:Object
        },
        allData:{
        	type:Array
        },
        bol:Boolean,
        introShowBol:Boolean
    },
    data(){
    	return{
    		show1Bol:true,
    		show2Bol:false
    	}
    },
    methods:{
    	show1:function(){
    		this.show1Bol = true;
    		this.show2Bol = false;
    	},
    	show2:function(){
    		this.show1Bol = false;
    		this.show2Bol = true;
    	},
    	back:function(){
    		this.$emit('onShow');
    	}
    }
}




</script>


