<template>
  <div class="class">
  	<!-- 头部 -->
    <v-header title="分类"></v-header>
    <!-- 头部按钮 -->
    <ul class="class-h-btn">
        <li style="color: #f29106" @click="tab1">全部
        </li>
        <li @click="tab2">价格从高到低
            <img :src="img" :class="{img1:img1_bol}">
        </li>
        <li  @click="tab3">销量优先
            <img :src="img" :class="{img2:img2_bol}">
        </li>
    </ul>
    <!-- 子按钮 -->
    <ul class="all" v-bind:class="{show:isShow}">
        <li @click="isShow=!isShow"><router-link to="/class/special">特卖区</router-link></li>
        <li @click="isShow=!isShow"><router-link to="/class/big">大电器</router-link></li>
        <li @click="isShow=!isShow"><router-link to="/class/small">小电器</router-link></li>
    </ul>
    <!-- 内容区 -->
    <router-view :h1="img1_bol" :h2="img2_bol"></router-view>
  </div>
</template>

<style type="text/css" scoped>
    .class{
        width: 100%;
        background: #f2f2f2;
    }
	.class-h-btn{
		width: 100%;
		display: flex;
		border-bottom: 1px solid #ccc;
    	background: white;
        position: fixed;
        top: 42px;
        z-index: 10;
	}
    .class-h-btn>li{
        flex: 1;
        display: block;
        text-align: center;
        height: 42px;
        color: #808080;
        font-size: 16px;
        line-height: 42px;
        position: relative;
        z-index: 10;
    }
    .class-h-btn img{
        width: 15%;
        position: relative;
        top:3px;
        left: -3px;
        transition: all .2s;
    }
    .img1{
        transform: rotateX(180deg);
    }
    .img2{
        transform: rotateX(180deg);
    }
    .all{
        width: 100%;
        display: flex;
        border-bottom: 1px solid #ccc;
        background: white;
        position: fixed;
        top: 42px;
        z-index: 2;
        transition: all .3s ease;
    }
    .show{
        top: 84px;
    }
    .all>li{
        flex: 1;
        display: block;
        text-align: center;
        height: 42px;
        line-height: 42px;
    }
    .all>li>a{
        background: #f2f2f2;
        font-size: 16px;
        padding:8px;
        border-radius: 3px;
        color: black;
    }
    .all .router-link-active{
        background: #f29106;
        color: white;
    }
</style>

<script type="text/javascript">
import vHeader from '../../component/header/header.vue';
import arrows from './img/arrows.png';

export default{
    components: {
        vHeader,
    },
    data:function(){
        return {
            img1_bol:false,
            img2_bol:false,
            img:arrows,
            isShow:false,
        }
    },
    methods:{
        tab1:function(){
            if (this.isShow) {
                this.isShow = false;
            }else{
                this.isShow = true;
            }
        },
        tab2:function(){
            if (this.img1_bol) {
                this.img1_bol = false;
            }else{
                this.img1_bol = true;
            }
        },
        tab3:function(){
            if (this.img2_bol) {
                this.img2_bol = false;
            }else{
                this.img2_bol = true;
            }
        }
    }
}
</script>




