<template>
  <div class="bottom" ref="bottom">
    <div class="space1">
      <router-link to="/shop"><img class="shopcar" src="../../common/img/shopcar.png" alt=""></router-link>
    </div>
    <div class="space2">
      <button class="add" @click="show_shopcar_detail">加入购物车</button>
    </div>
    <div class="space2">
      <button class="buy">立即购买</button>
    </div>
    <shopcarshow ref="shopcarshow" :toshopcarshow="send_to_shopcarshow"></shopcarshow>
  </div>
</template>
<style scoped>
  /*底部购物车开始*/
  .bottom {
    position: fixed;
    display: flex;
    align-items: center;
    width: 100%;
    height: 50px;
    bottom: 0;
    background: gainsboro;
  }

  .bottom .space1 {
    flex: 1;
    text-align: center;
  }

  .bottom .space2 {
    flex: 2;
    text-align: center;
  }

  .bottom .space1 .shopcar {
