  .bottom .space2 .add, .bottom .space2 .buy {
    width: 106px;
    height: 32px;
    border: none;
    border-radius: 6px;
    font-size: 15px;
    color: white;
  }

  .bottom .space2 .add {
    background: #ed9d2a;
  }

  .bottom .space2 .buy {
    background: #ee3749;
  }

  /*底部购物车结束*/
</style>
<script type="text/javascript">

  import Shopcarshow from 'pages/home/shopcarshow.vue'
  export default {
    props: ['sendfood'],
    data(){
      return {
        send_to_shopcarshow: {}
      }
    },
    methods: {
      show_shopcar_detail: function () {
        this.send_to_shopcarshow = this.sendfood;
        var _this = this;
        this.$parent.$parent.shopcaradding(function () {
//          _this.$refs.shopcarshow.show_up();
        });
        this.$refs.shopcarshow.show_up();
//        console.log('shopcar接收到数据', this.sendfood);
      },
      hide_shopcar_detail: function () {
        this.$refs.shopcarshow.to_close();
      }
    },
    components: {
      Shopcarshow
    },
    computed: {
      addresult: function () {

      }
    }
  }
</script>



