<template>
  <transition name="fade">
    <div v-show="showHide" class="detailed">
      <div class="content">
        <div class="mark"></div>
        <div class="image_header">
          <img :src="item.Img" alt="">
          <div class="back" @click="hide">
            <i class="iconfont icon-llmainpageback"></i>
          </div>
          <div class="collect">
            <i class="iconfont icon-shoucang" :class="{ active:bol }" @click="activeCl"></i>
          </div>
          <div class="share">
            <i class="iconfont icon-share"></i>
          </div>
        </div>
        <div class="des">
          <p class="text">{{item.text}}</p>
          <p class="det">
            <span class="price">￥{{item.price}}</span>
            <span class="prime">原价：<span>￥{{item.prime}}</span></span>
            <span class="num">销量：{{item.num}}</span>
          </p>
          <p class="buy">
            <span class="timeCount">{{item.time|timeCount}}</span>
            <span class="limit">限量：{{item.limit}}件</span>
          </p>
        </div>
        <div class="subcon">
          <span @click="showSP" class="span" :class="{active:active}">商品详情</span>
          <span @click="showUS" class="span" :class="{active1:active1}">用户评价</span>
        </div>
        <ul class="shopping" ref="shopSP">
          <li>
            <img :src="item.Img" alt="">
            <span>{{item.text}}</span>
          </li>
          <li>
            <img :src="item.Img" alt="">
            <span>{{item.text}}</span>
          </li>
          <li>
            <img :src="item.Img" alt="">
            <span>{{item.text}}</span>
          </li>
        </ul>
        <ul class="user" ref="shopUS">
          <li>
            <span class="span1">{{item.name}}</span>
            <span class="span2">{{item.date}}</span>
            <p>{{item.com}}</p>
          </li>
          <li>
            <span class="span1">{{item.name}}</span>
            <span class="span2">{{item.date}}</span>
            <p>{{item.com}}</p>
          </li>
          <li>
            <span class="span1">{{item.name}}</span>
            <span class="span2">{{item.date}}</span>
            <p>{{item.com}}</p>
          </li>
          <li>
            <span class="span1">{{item.name}}</span>
            <span class="span2">{{item.date}}</span>
            <p>{{item.com}}</p>
          </li>
          <li>
            <span class="span1">{{item.name}}</span>
            <span class="span2">{{item.date}}</span>
            <p>{{item.com}}</p>
          </li>
          <li>
            <span class="span1">{{item.name}}</span>
            <span class="span2">{{item.date}}</span>
            <p>{{item.com}}</p>
          </li>
        </ul>
      </div>
      <div class="shop">
        <div ref="counts" class="cricle">{{counts}}</div>
        <i class="iconfont icon-gouwuche"></i>
        <span class="span">立即购买</span>
        <span @click="buy" class="span shop1">加入购物车</span>
      </div>
    </div>
  </transition>
</template>

<style scoped>
  @import "css/iconfont.css";

  .detailed {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1000;
    width: 100%;
    background: #ffffff;
  }

  .detailed .content {
    padding-bottom: 56px;
  }

  .detailed .shop {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 45px;
    background: #f2f2f2;
    padding-top: 10px;
    padding-left: 8px;
  }

  .fade-enter-active, .fade-leave-active {
    transition: all 0.2s linear;
    transform: translate3d(0, 0, 0);
  }

  .fade-enter, .fade-leave-active {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }

  .detailed .image_header {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
  }

  .detailed .image_header img {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
  }

  .detailed .image_header i {
    display: block;
    padding: 10px;
    font-size: 25px;
    color: #ffffff;
  }

  .detailed .image_header .back {
    position: absolute;
    top: 10px;
    left: 0;
    z-index: 30;
  }

  .detailed .image_header .collect {
    position: absolute;
    top: 10px;
    right: 50px;
    z-index: 30;
  }

  .detailed .image_header .share {
    position: absolute;
    top: 10px;
    right: 0;
    z-index: 30;
  }

  .detailed .mark {
    position: absolute;
    background: -webkit-linear-gradient(rgba(225, 225, 225, 1), rgba(0, 0, 0, 0));
    width: 100%;
    height: 100px;
    z-index: 20;
  }

  .detailed .content .des {
    margin-top: 15px;
    border-bottom: 12px solid #f2f2f2;
    padding: 8px;
    font-size: 14px;
    color: #666666;
  }

  .detailed .des .det {
    margin-top: 10px;
    padding-bottom: 8px;
    border-bottom: 1px solid #d9d9d9;
  }

  .detailed .des .price {
    font-size: 16px;
    color: #ff3f12;
  }

  .detailed .des .prime {
    margin-left: 15px;
  }

  .detailed .des .prime > span {
    text-decoration: line-through;
  }

  .detailed .des .num {
    float: right;
  }

  .detailed .des .buy {
    padding-top: 10px;
  }

  .detailed .des .limit {
    float: right;
  }

  .detailed .subcon {
    position: relative;
    display: flex;
  }

  .detailed .subcon .span {
    height: 32px;
    line-height: 32px;
    display: inline-block;
    flex: 1;
    text-align: center;
    font-size: 16px;
    color: #595959;
  }

  .detailed .shopping {
    border-top: 2px solid #cecece;
    margin-top: -2px;
    width: 100%;
  }

  .detailed .shopping li {
    width: 100%;
    margin-left: 0;
  }

  .detailed .shopping li img {
    width: 100%;
  }

  .detailed .shopping li span {
    font-size: 18px;
  }

  .detailed .subcon .span.active {
    border-bottom: 3px solid #ff3b0e;
  }

  .detailed .subcon .span.active1 {
    border-bottom: 3px solid #ff3b0e;
  }

  .detailed .user {
    border-top: 2px solid #cecece;
    margin-top: -2px;
    display: none;
    width: 100%;
  }

  .detailed .shop .cricle {
    display: none;
    position: absolute;
    width: 20px;
    height: 20px;
    background: red;
    text-align: center;
    line-height: 25px;
    border-radius: 50%;
    margin-left: 28px;
    font-size: 12px;
    color: #ffffff;
  }

  .detailed .shop .span {
    display: inline-block;
    border-radius: 5px;
    width: 110px;
    height: 35px;
    background: #ee3749;
    text-align: center;
    line-height: 35px;
    font-size: 18px;
    color: #ffffff;
    float: right;
    margin-right: 20px;
  }

  .detailed .shop .shop1 {
    margin-right: 18px;
    background: #ed9d2a;
  }

  .detailed .shop i {
    color: #262626;
    font-size: 42px;
  }

  .detailed .content .collect .active {
    color: yellow;
  }

  .detailed .user li {
    margin-left: 0;
    width: 100%;
    height: 60px;
    border-bottom: 1px solid #d9d9d9;
    font-size: 14px;
  }

  .detailed .user li .span1 {
    margin-top: 10px;
    display: inline-block;
    margin-left: 8px;
  }

  .detailed .user li .span2 {
    margin-top: 10px;
    margin-right: 8px;
    color: #666666;
    float: right;
    display: inline-block;
  }

  .detailed .user li p {
    margin-top: 12px;
    font-size: 16px;
    margin-left: 8px;
  }
</style>

<script>
  export default{
    props: {
      item: {}
    },
    data() {
      return {
        showHide: false,
        bol: false,
        counts: 0,
        active: true,
        active1: false
      }
    },
    filters: {
      timeCount: function (value) {
        var now = new Date();
        var diff = parseInt((value - now.getTime()) / 1000);
        var days = parseInt(diff / (24 * 3600));
        diff = diff % (24 * 3600);
        var hours = parseInt(diff / 3600);
        diff = diff % 3600;
        var minutes = parseInt(diff / 60);
        var seconds = diff % 60;
        var timer = "限时：" + days + "天" + hours + "小时" + minutes + "分钟" + seconds + "秒";
        return timer;
      }
    },
    methods: {
      show() {
        this.showHide = true;
      },
      hide() {
        this.showHide = false;
        this.$emit('hide');
      },
      showSP(){
        this.$refs.shopUS.style.display = 'none';
        this.$refs.shopSP.style.display = 'block';
        this.active1 = false;
        this.active = true;
      },
      showUS(){
        this.$refs.shopSP.style.display = 'none';
        this.$refs.shopUS.style.display = 'block';
        this.active1 = true;
        this.active = false;
      },
      activeCl() {
        if (this.bol) {
          this.bol = false;
          alert("取消收藏！");
        } else {
          this.bol = true;
          alert("收藏成功！");
        }
      },
      buy() {
        this.counts += 1;
        this.$refs.counts.style.display = 'block';
      }
    }
  }
</script>



