<template>
  <transition name="fade">
    <div class="food" v-show="detailshow">
      <header1 title="food">
        <span slot="back" class="back" @click="hidedetail">返回</span>
      </header1>
      <placeholder></placeholder>
      <div>
        <div class="swiper-container swiper-container2">
          <div class="swiper-wrapper">
            <div class="swiper-slide"><img src="../../../src/common/img/homepic1.jpg" alt="picture"></div>
            <div class="swiper-slide"><img src="../../../src/common/img/homepic1.jpg" alt="picture"></div>
            <div class="swiper-slide"><img src="../../../src/common/img/homepic1.jpg" alt="picture"></div>
          </div>
          <div class="swiper-pagination swiper-pagination2"></div>

        </div>
      </div>
      <div class="menu-wrap" ref="foodmenu">
        <div class="menu-list">
          <ul>
            <li v-for="(items,index) in goods" ref="li" @click="getindex(index)"
                :class="{'active':currentindex===index}">
              <span>{{items.name}}</span>
            </li>
          </ul>
        </div>
        <div class="menu-content" ref="menuul">
          <ul ref="ul" v-for="(items,index) in goods" v-show="currentindex===index">
            <h4>--{{items.name}}--</h4>
            <li v-for="item in items.foods" @click="showfooddetail(item)">
              <div>
                <img width="90" :src="item.icon" alt="">
              </div>
              <div class="description">
                <h5>{{item.name}}</h5>
                <span>${{item.price}}</span>
                <p>已售：<span>{{item.sellCount}}</span></p>
                <p>热度：{{item.rating}}</p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <fooddetail ref="foods" :foods="foodsdetail"></fooddetail>
    </div>
  </transition>
</template>
<style scoped>
  .food {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0px;
    z-index: 100;
    background: white;
  }

  .swiper-slide img {
    width: 100%;
  }

  .fade-enter-active, .fade-leave-active {
    transition: all 0.2s linear;
    transform: translate3d(0, 0, 0);
  }

  .fade-enter, .fade-leave-active {
    opacity: 1;
    transform: translate3d(100%, 0, 0);
  }

  .back {
    position: absolute;
    left: 10px;
    font-size: 20px;
  }

  .menu-wrap {
    display: flex;
    /*position: relative;*/
  }

  .menu-list {
    flex: 2;
    overflow-y: scroll;
    -webkit-overflow-scrolling : touch;
  }

  .menu-list li {
    height: 60px;
    display: flex;
    text-align: center;
    font-size: 15px;
    word-wrap: break-word;
    padding-left: 10px;
  }

  .menu-list li span {
    margin-top: 20px;
  }

  .menu-content {
    flex: 5;
    overflow-y: scroll;
    -webkit-overflow-scrolling : touch;
  }

  .menu-content h4 {
    font-size: 15px;
    margin: 10px;
    color: gray;
  }

  .menu-content li {
    display: flex;
    margin: 10px;
  }

  .menu-content .description {
    margin-left: 8px;
  }

  .menu-content .description h5 {
    font-size: 15px;
    margin: 8px 0;
  }

  .menu-content .description p {
    font-size: 13px;
    line-height: 15px;
    color: #a6a4a4;
    margin: 5px 0;
  }

  .menu-content .description span {
    color: red;
    font-size: 15px;
  }

  .active {
    background: white;
    border-radius: 6px;
    background: #eb714e;
  }

  .move-enter {
    transition: all 0.2s;
    transform: translate3d(10px);
  }

  .move-enter-active {
    transform: translate3d(0);
  }
</style>
<script type="text/javascript">
  import Header1 from 'component/header/header.vue'
  import Placeholder from 'pages/home/placeholder.vue'
  import Fooddetail from 'pages/home/fooddetail.vue'
  export default {
    props: ['shipin', 'goods'],
    data() {
      return {
        scroll: {},
        currentindex: 0,
        detailshow: false,
        foodshow: false,
        foodsdetail: {},
        //购物车数组，不能用，每次重新加载这个页面，data里面的变量会重新清空！！！
        //shopcaradding_arr: [],
      }
    },
    components: {
      Header1,
      Placeholder,
      Fooddetail
    },
    created: function () {

    },
    methods: {
      //判断是否重复
      isrepeat: function (arr2, m) {
        for (var i = 0; i < arr2.length; i++) {
          if (arr2[i].name == m.name) {
            arr2[i].count++;
            return true
          }
        }
        return false
      },
      //重复的数据不添加
      singlearr: function (arr) {
        var arr2 = [];
        for (var j = 0; j < arr.length; j++) {
          if (!this.isrepeat(arr2, arr[j])) {
            arr2.push(arr[j])
          }
        }
        return arr2
      },
      shopcaradding: function (cb) {
        //组件刷新时，会将export default中的data里面的变量值清空
        //先获取本地存储数据，若没有则为空--"[]"，
        var str = localStorage.getItem('savedata') || "[]";
        //将获取到的数据转成json形式
        var savedata = JSON.parse(str);
        console.log(savedata)
        var num = savedata.length;
        //给本组件的foodsdetail增加一个新属性
        this.foodsdetail.count=1
        savedata.push(this.foodsdetail);
        var savedata = this.singlearr(savedata);
        console.log('有重复？',savedata);
        if(num<savedata.length){
            cb()
        }
        var savedata = JSON.stringify(savedata);
        //详情页数据存入localStorage
        localStorage.setItem('savedata', savedata);

      },
      showfooddetail: function (item) {
        this.foodsdetail = item;
        //给点击获取到的对象添加一个用来计算的属性
        this.foodsdetail.count = 1;
        this.$refs.foods.showfood();
      },
      getoffT: function () {
        this.$refs.foodmenu.style.height = window.innerHeight - this.$refs.foodmenu.offsetTop + 'px';
      },
      getindex: function (index) {
        this.currentindex = index;
        return index
      },
      hidedetail: function () {
        this.detailshow = false
      },
      showfood1: function () {
        this.detailshow = true;
        setTimeout(function () {
          new Swiper(".swiper-container2", {
            direction: 'horizontal',
            loop: true,
            pagination: '.swiper-pagination2',
            autoplay: 1500,
            autoplayDisableOnInteraction: false,
          });
        }, 1)
      }
    },
    updated: function () {
      this.getoffT();
    },
    mounted: function () {

    },
  }
</script>



