<template>
  <div class="star" >
    <span v-for="itemclass in itemclasses" class="star-item" :class="itemclass"></span>
  </div>
</template>
<script type="text/javascript">
  const Length = 5;
  const cls_on = 'on';
  const cls_half = 'half';
  const cls_off = 'off';
  export default {
    props: {
      size: {
        type: Number
      },
      score: {
        type: Number
      }
    },
    computed: {
      itemclasses: function () {
        let result = [];
        let score = Math.floor(this.score*2)/2;
        let xiaoshu = score%1 !== 0;
        let zhengshu = Math.floor(score);
        for(let i=0;i<zhengshu;i++){
            result.push(cls_on)
        };
        if(xiaoshu){
            result.push(cls_half)
        };
        while (result.length<Length){
            result.push(cls_off)
        };
        return result;
      }
    }
  }
</script>
<style scoped>
  .star {
    font-size: 0px;
    display: flex;
    width: 100%;
    height: 100%;
  }

  .star .star-item {
    flex: 1;
    background-repeat: no-repeat;
    width: 20px;
    height: 100%;
  }

  .star .star-item:last-child {
    margin-right: 0;
  }

  .star .on {
    background: url("../../common/img/star48_on@3x.png");
    background-size: 100% 100%;
  }

  .star .off {
    background: url("../../common/img/star48_off@3x.png");
    background-size: 100% 100%;
  }

  .star .half {
    background: url("../../common/img/star48_half@3x.png");
    background-size: 100% 100%;
  }
</style>



