// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import VueRouter from 'vue-router';
import App from './App';
import Home from './pages/home/home.vue';
import Class from './pages/class/class.vue';
import My from './pages/my/my.vue';
import Member from './pages/member/member.vue';
import Shop from './pages/shop/shop.vue';
import Life from './pages/home/life.vue';
import special from './pages/class/usbPages/special.vue';
import big from './pages/class/usbPages/big.vue';
import small from './pages/class/usbPages/small.vue';
import Basic from './pages/my/basic.vue';
import Order from './pages/my/order.vue';
import Name from './pages/my/name.vue';
import Contact from './pages/my/contact.vue';
import Editing from './pages/my/editing.vue';
import Password from './pages/my/password.vue';
import Walletpassword from './pages/my/walletpassword.vue';
import Supply from './pages/my/supply.vue';
import Orderdetail from './pages/my/orderdetail.vue';
import Myorder from './pages/my/myorder.vue';
import Mywallet from './pages/my/mywallet.vue';
import Toup from './pages/my/toup.vue';


import VueResource from 'vue-resource';



/* eslint-disable no-new */
Vue.use(VueRouter)
Vue.use(VueResource)
const router = new VueRouter({
  RouterLinkActive: 'active',
  routes: [
    {
      path: '/class/special', component: Class,
      children:[
        {path:'/class/special', component: special},
        {path: '/class/big', component: big},
        {path: '/class/small', component: small}
      ]
    },
    {path: '/home', component: Home,
      children:[
        {path:'/life',component:Life}
      ]
    },
    {path: '/member', component: Member},
    {path: '/shop', component: Shop},
    {path: '/my', component: My},

    {path: '/basic', component: Basic},
    {path: '/order', component: Order},
    {path: '/name', component: Name},
    {path: '/contact', component: Contact},
    {path: '/editing', component: Editing},
    {path: '/password', component: Password},
    {path: '/walletpassword', component: Walletpassword},
    {path: '/supply', component: Supply},
    {path: '/orderdetail', component: Orderdetail},
    {path: '/myorder', component: Myorder},
    {path: '/mywallet', component: Mywallet},
    {path: '/toup', component: Toup}

  ]
})
router.push('/home');
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})


