<template>
  <transition name="fadee">
    <div class="fooddetail" v-show="foodshow">
      <header1 title="fooddetail">
        <span slot="back" class="back" @click="hidefood">返回</span>
      </header1>
      <placeholder ref="placeholder"></placeholder>
      <div class="wrap" ref="wrap">
        <div class="img">
          <img style="width: 100%" :src="foods.image" alt="">
        </div>
        <div class="title">
          <h5>{{foods.name}}</h5>
          <div>
            <span class="price">${{foods.price}}</span>
            <span class="oldprice">原价：${{foods.price*1.5}}</span>
            <span class="sales">销量：{{foods.sellCount}}</span>
          </div>
        </div>
        <!--<p class="description">{{foods.info}}</p>-->
        <div class="member">
          <div class="detail" :class="{'active':bol1}" @click="beactive1">商户详情</div>
          <div class="man" :class="{'active':bol2}" @click="beactive2">用户评价</div>
        </div>
        <div class="sellerdescription" v-show="bol1">

        </div>
        <div class="memberdescription" v-show="bol2">
          <ul>
            <li v-for="items in foods.ratings">
              <span class="name">用户：{{items.username}}</span>
              <span class="time">时间：{{time}}</span>
              <div class="star_wrap">
                <star :score="4.8"></star>
              </div>
            </li>
          </ul>

        </div>
      </div>
      <shopcar ref="shopcar" :sendfood="importfoods"></shopcar>

    </div>
  </transition>
</template>
<style scoped>
  .active {
    color: red;
  }

  .back {
    position: absolute;
    left: 10px;
    font-size: 20px;
  }

  .fooddetail {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 120;
    background: #f2f2f2;
  }

  .fadee-enter-active, .fadee-leave-active {
    transition: all 0.2s linear;
    transform: translate3d(0, 0, 0);
  }

  .fadee-enter, .fadee-leave-active {
    opacity: 1;
    transform: translate3d(0, 100%, 0);
  }

  .wrap {
    padding-bottom: 50px;
    overflow-y: scroll;
    -webkit-overflow-scrolling : touch;
  }

  .wrap .title, .wrap .description {
    margin: 15px 8px 15px 8px;
    background: white;
    border-radius: 4px;
  }

  .wrap .title h5 {
    font-size: 20px;
    padding: 15px 0 8px 0;
    margin-bottom: 4px;
    border-bottom: 1px solid lightgrey;
  }

  .wrap .title div {
    display: flex;
    align-items: center;
    font-size: 15px;
    margin-bottom: 15px;
    padding: 4px;
    border-radius: 4px;
  }

  .wrap .title .price {
    flex: 1;
    font-size: 20px;
    color: red;
  }

  .wrap .title .oldprice {
    flex: 2;
    text-decoration: line-through;
  }

  .wrap .title .sales {
    flex: 1;
    padding-right: 15px;
    text-align: end;
  }

  .wrap .description {
    padding: 8px 4px;
    font-size: 15px;
    line-height: 20px;
  }

  .wrap .member {
    display: flex;
    align-items: center;
    margin: 0px 8px 15px 8px;
  }

  .wrap .member .detail, .wrap .member .man {
    flex: 1;
    text-align: center;
    background: white;
    font-size: 15px;
    padding: 13px 0;
    border-bottom: 1px solid darkgrey;
  }

  .wrap .memberdescription {
    margin: 0px 8px 15px 8px;
    padding: 15px 8px;
    background: white;
  }

  .wrap .memberdescription .name {
    font-size: 13px;
  }

  .wrap .memberdescription .time {
    font-size: 13px;
    width: 50px;
  }

  .star_wrap {
    width: 50px;
    height: 10px;
  }



</style>
<script type="text/javascript">
  import Header1 from 'component/header/header.vue'
  import Placeholder from 'pages/home/placeholder.vue'
  import Star from 'pages/home/star.vue'
  import Shopcar from 'pages/home/shopcar.vue'
  export default {
    props: ['foods'],
    data() {
      return {
        foodshow: false,
        bol1: true,
        bol2: false,
        importfoods:{},
        time: ''
      }
    },
    computed: function () {
      this.setheight;
    },
    components: {
      Header1,
      Placeholder,
      Star,
      Shopcar
    },
    methods: {
      gettime: function () {
        var T = new Date();
        this.time = T.getFullYear()+'-'+T.getMonth()+'-'+T.getDate()
      },
      beactive1: function () {
        this.bol1 = true;
        this.bol2 = false;
      },
      beactive2: function () {
        this.bol1 = false;
        this.bol2 = true;
        this.setheight();
        this.gettime();

        console.log('fooddetail的wrap高度', this.$refs.wrap.offsetHeight)
//        console.log(this.time)
      },
      showfood: function () {
        this.foodshow = true;
        this.importfoods = this.foods;
        var _this = this;
//        setTimeout(function () {
//          console.log('fooddetail组件接收到父级组件food的数据',_this.foods);
//          console.log('fooddetail组件--->组件shopcar',_this.importfoods);
//        },1)
      },
      hidefood: function () {
        this.foodshow = false;
        this.bol1 = true;
        this.bol2 = false;
        this.$refs.shopcar.hide_shopcar_detail();
//        console.log(this.foods)
      },
      setheight: function () {
        this.$refs.wrap.style.height = window.innerHeight - this.$refs.placeholder.$el.offsetHeight - 50+ 'px';
//        console.log("snv", this.$refs.bottom)
      }
    },
    mounted: function () {
      console.log(this.foods)
    },
    updated: function () {

    }
  }
</script>



