<template>
  <div class="member">
    <v-header title="购物车"></v-header>
    <placeholder></placeholder>
    <ul class="shopcar_list">
      <li v-for="(item,index) in this.shopcardata">
        <div class="select" @click="select(item)">
          <img class="chosed" v-if="item.bol" src="../../common/img/chosed.png" alt="">
          <img class="notchosed" v-else src="../../common/img/notchosed.png" alt="">
        </div>
        <div class="pic">
          <img :src="item.icon">
        </div>
        <div class="list_description">
          <p>{{item.name}}</p>
          <p class="item_price">${{item.price}}</p>
        </div>
        <div class="count">
          <span v-tap="{fn: data_count_down, data: item}">-</span><strong>{{item.count}}</strong><span
          v-tap="{fn: data_count_up, data: item}">+</span>
        </div>
      </li>

    </ul>
    <div class="bottom" v-show="come_out">
      <div class="all" @click="select_all">
        <img class="chosed" v-if="all" src="../../common/img/chosed.png" alt="">
        <img class="notchosed" v-else src="../../common/img/notchosed.png" alt="">
        全选
      </div>
      <div class="clearbutton">
        <button class="clear" v-if="all" @click="clear_all" :class="{actived:be_active}">删除全部</button>
        <button class="clear" v-else @click="clear" :class="{actived:be_active}">删除</button>
      </div>
      <div class="totalprice">
        <span>合计: ${{sum}}</span>
      </div>
      <div class="pay">
        <span :class="{active:be_active}">结算</span>
      </div>
    </div>
  </div>
</template>
<style type="text/css" scoped>
  .shopcar_list {
    margin: 0 6px 50px 6px;
    overflow-y: scroll;
    -webkit-overflow-scrolling : touch;
  }

  .shopcar_list li {
    display: flex;
    height: 60px;
    margin: 6px 0;
    border-bottom: 1px solid darkgray;
  }

  .shopcar_list li img {
    width: 70%;
    border-radius: 10%;
  }

  .shopcar_list li .select {
    flex: 1;
    position: relative;
  }

  .shopcar_list li .select .chosed, .shopcar_list li .select .notchosed {
    position: absolute;
  }

  .shopcar_list li .pic {
    flex: 2;
    text-align: center;
  }

  .shopcar_list li .list_description {
    flex: 6;
  }

  .shopcar_list li .list_description .item_price {
    margin-top: 6px;
    font-size: 16px;
    color: red;
  }

  .shopcar_list li .count {
    flex: 3;
  }

  .shopcar_list li .count span {
    display: inline-block;
    margin: 0 4px;
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    border-radius: 50%;
    background: #eb714e;
  }

  .bottom {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 50px;
    height: 50px;
    display: flex;
    background: #4b4b4b;
  }

  .bottom .all {
    flex: 1;
    font-size: 13px;
    line-height: 50px;
  }

  .bottom .all img {
    width: 30%;
    margin-left: 4px;
    vertical-align: middle;
  }

  .bottom .clearbutton {
    flex: 1.5;
    display: flex;
  }

  .bottom .clearbutton .clear {
    margin: 12px 0 12px 4px;
    border: none;
    border-radius: 4px;
    font-size: 12px;
    background: gainsboro;
  }
  .bottom .clearbutton .actived{
    margin: 12px 0 12px 4px;
    border: none;
    border-radius: 4px;
    font-size: 12px;
    color: white;
    background: #eb714e;
  }
  .bottom .totalprice {
    flex: 2;
  }

  .bottom .totalprice span {
    line-height: 50px;
    margin-left: 4px;
    font-size: 15px;
  }

  .bottom .pay {
    flex: 1;
  }

  .bottom .pay span {
    line-height: 50px;
    font-size: 13px;
    padding: 6px 16px;
    border-radius: 4px;
    color: grey;
    background: gainsboro;
  }
  .bottom .pay .active{
    line-height: 50px;
    font-size: 13px;
    padding: 6px 16px;
    border-radius: 4px;
    color: white;
    background: #eb714e;
  }
</style>
<script>
  import vHeader from '../../component/header/header.vue'
  import Placeholder from 'pages/home/placeholder.vue'
  export default{
    data() {
      return {
        shopcardata: [],
        checked: [],
        checkedNames: [],
        totalprice: 0,
        all: false,
        count: 0,
        sum: 0,
        come_out:false,
        be_active:false,
      }
    },
    components: {
      vHeader,
      Placeholder
    },
    computed: {},
    methods: {
      select: function (item) {
        item.bol = !item.bol;
        //选择时，调用check_all来检测是否有未选中的
        //在check_all内部再调用change_color看看是否有选中的以激活结算按钮
        this.check_all();
        var str = JSON.stringify(this.shopcardata);
        localStorage.setItem('savedata', str);
      },
      select_all:function () {
        this.all =!this.all;
        //和全选按钮的布尔值保持一致
        this.be_active = this.all;
        for(var i=0;i<this.shopcardata.length;i++){
            this.shopcardata[i].bol = this.all;
        }
        this.sumPrice();
        if(this.shopcardata.length==0){
            this.all = false;
        }
      },
      change_color:function () {
        for(var i=0;i<this.shopcardata.length;i++){
            if(this.shopcardata[i].bol==true){
                //检测如果有选中就激活结算按钮颜色
                this.be_active = true;
                break;
            }
        }
      },
      check_all: function () {
        for (var i = 0; i < this.shopcardata.length; i++) {
          if (this.shopcardata[i].bol == false) {
            break
          }
        }
        this.be_active = this.all = i == this.shopcardata.length;
        this.sumPrice();
        this.change_color();
      },
      sumPrice: function () {
        //初始化总价和总数量
        var sum = 0;
        this.count = 0;
        for (var i = 0; i < this.shopcardata.length; i++) {
          if (this.shopcardata[i].bol) {
            sum += this.shopcardata[i].count * this.shopcardata[i].price;
            this.count += this.shopcardata[i].count
          }
        }
        this.sum = sum;
        var str = JSON.stringify(this.shopcardata);
        localStorage.setItem('savedata', str);
      },
      clear: function () {
        for (var i = 0; i < this.shopcardata.length; i++) {
          if (this.shopcardata[i].bol) {
            this.shopcardata.splice(i, 1);
            i--;
          }
        }
        this.sumPrice();
        //点击删除，调用check_all()，检测是否有选中的项以判断结算按钮的激活状态
        this.check_all();
      },
      clear_all: function () {
        this.shopcardata = [];
        this.sumPrice();
        if(this.shopcardata.length==0){
          this.all = false;
        }
        this.show_or_hide();
      },
      data_count_up: function (item) {
        item.count++;
        this.sumPrice();
        console.log(item.count, item.bol);

      },
      data_count_down: function (item) {
        if (item.count == 1) {
          item.count = 1
        } else {
          item.count--;
        }
        this.sumPrice();
      },
      getdata: function () {
        //本地存储，详情页的内容
        var data = JSON.parse(localStorage.getItem('savedata'));
        //加入bol属性
        for (var i = 0; i < data.length; i++) {
            if(data[i].bol=='undefined'||data[i].bol==null){
              data[i].bol = false;
            }

        };
        //赋值给自身组件data中的变量
        this.shopcardata = data;
      },
      show_or_hide: function () {
        if (this.shopcardata.length!=0) {
          this.come_out = true
        } else {
          this.come_out = false
        }
      },
    },
    mounted: function () {
      this.getdata();
      this.check_all();
      this.show_or_hide();
      this.change_color();
    },
    directives: {
      tap: {
        bind: function (el, binding, obj) {
          // console.log(obj);
          el.addEventListener("touchstart", function (e) {
            el.isMoved = false;
            e.preventDefault();
          })
          el.addEventListener("touchmove", function () {
            el.isMoved = true;
          })
          el.addEventListener("touchend", function () {
            if (!el.isMoved) {
              binding.value.fn(binding.value.data);
            }
          })
        }
      }
    }
  }
</script>



