<template>
  <transition name="go-up">
    <div class="background" v-show="detail_show_up">
      添加成功
    </div>
  </transition>

</template>
<style scoped>
  .background {
    position: fixed;
    top: 42px;
    left: 0;
    right: 0;
    height: 50px;
    background: rgba(0, 0, 0, 0.7);
    color: white;
    font-size: 20px;
    text-align: center;
    line-height: 50px;
  }
  .go-up-enter-active, .go-up-leave-active {
    transition: all 0.2s linear;
    transform: translate3d(0, 0, 0);
  }

  .go-up-enter, .go-up-leave-active {
    opacity: 0;
    transform: translate3d(0, -10%, 0);
  }
</style>
<script type="text/javascript">
  export default {
    props: ['toshopcarshow'],
    data(){
      return {
        detail_show_up: false,
        timer:null,
      }
    },
    methods: {
      show_up: function () {
        this.detail_show_up = true;
        var _this = this;
        clearTimeout(this.timer);
        this.timer = setTimeout(function () {
          _this.to_close();
        },1000);
      },
      to_close: function () {
        this.detail_show_up = false
      }
    },
  }
</script>



