<template>
	<div class="password">
		<v-header title="修改密码"></v-header>
		<span class="return" @click="retu">&lt;</span>
		<div class="at_present"><input type="text" name="" class="text com" value="" placeholder="当前密码"/></div>
		<div class="new_password common_password"><input type="text" name="" class="text com" value=""
			 placeholder="新密码"/></div>
	    <div class="Confirm_Password common_password"><input type="text" name="" class="text com" value="" 
	    placeholder="确认密码" /></div>
	     <p>注意密码不能为空，可由英文和数字字母组成</p>
		<div class="box com">确定</div>
	</div>
</template>

<style scoped>
	.password {
		position: relative;
		background: #f2f2f2;
	}
	
	.password .return {
		position: absolute;
		top: -25px;
		color: #FFFFFF;
		margin-left: 10px;
		z-index: 100;
	}
	
	.password .at_present  {
		width: 100%;
		height: 50px;
		margin-top: 40px;
		font-size: 14px;
	}
	.password .com{
	width: 90%;
	height: 35px;
	margin-left: 17px;
	border-radius:5px;
}
	
	.password .at_present .text {
		display: inline-block;
		margin-top: 8px;
		border-radius: 5px;
	}
	
	.Confirm_Password{
		margin-top: 2px;
	}
	.common_password{
		font-size: 14px;
	}
	.password .box {
		margin-top: 320px;
		line-height: 35px;
		background: #f29004;
		text-align: center;
		color: #ffffff;
	}
	.password p{
		font-size: 14px;
		color: #a1a1a1;
		margin-left: 17px;
		margin-top: 13px;
	}
</style>

<script>
	import vHeader from '../../component/header/header.vue'

	export default {
		components: {
			vHeader
		},
		data() {
			return {}
		},
    methods:{
    	retu:function(){
    		this.$router.go(-1);
    	}
    }
	}
</script>


